.collapse {
	position: absolute;
	top: 100%;
	left: 0;
	width: calc(100% - 100px);
	padding: 50px;
	visibility: hidden;
	opacity: 0;
	background: $white;
	z-index: 1000;
	display: flex;
	justify-content: space-around;
	transition: all 200ms ease-in-out;
	border-bottom: 2px solid $primary;
}