.form {
	width: 100%;

	.row {
		width: 100%;
		display: flex;
		position: relative;
		padding: 20px 0;
	}

	.no-padding {
		padding: 0;
	}

	&-input {
		width: 100%;
		height: 40px;
		padding: 5px;
		box-sizing: border-box;
		font-family: 'Hind', sans-serif;
		font-weight: 500;
		color: $secondary;
		border: 1px solid $border;
		border-radius: 3px;
		outline: 0;

		&:focus {
			border: 2px solid $blue;
		}
	}

	&-label {
		position: absolute;
		top: 0;
		left: -10px;
		color: $primary;
		font-family: 'Lato', sans-serif;
		font-weight: 900;
		text-transform: uppercase;
	}

	&-btn {
		margin-left: 5px;

		&:first-child {
			margin: 0;
		}
	}

	&-text {
		color: $primary;
		text-decoration: none;
		font-family: 'Lato', sans-serif;
		font-weight: 900;
		text-align: center;
		text-transform: uppercase;
		width: 100%;
		transition: all 250ms;

		&:hover {
			color: $blue;
		}
	}
}